<template>
<div class="home">
    <div class="container">
        <div class="tabwrap">
            <TAB v-for="(item,index) of grouplist" :key="index" :tabname="item[1]" :isclicked="currentindex==index" @tabclickhandler="tabclick(index)"></TAB>
            <div class="addtabbtn" @click="addgroup">
                <i class="ri-add-line"></i>
                <addgroup v-show="addgroupisclick" @closehandler="close"></addgroup>
            </div>
        </div>
        <div class="tabitemwrap">
            <div class="controlarea">
                <input class="searchbar" type="text" placeholder="搜尋標籤">
                <div class="addbtn" @click="addtab">
                    <img src="../assets/add-fill.svg" alt="">
                </div>
            </div>
            <div class="tabaera">
                <div class="wrap" v-for="(item,index) of tablist" :key="index">
                    <Windowtab v-show="index>0&&currentindex==item[2]" :tabname="item[0]" :url="item[1]"></Windowtab>
                </div>

            </div>

        </div>
    </div>

    <addtab v-show="addisclick" :groupindex="currentindex" @closeaddtabhandler="closeaddtab"></addtab>

</div>
</template>

<script>
import TAB from '../components/Tab'
import Windowtab from '../components/Windowtab'
import addtab from '../components/Addtab'
import axios from 'axios'
import addgroup from '../components/Addgroup'
import liff from '@line/liff';
// @ is an alias to /src

export default {
    name: 'Home',
    data() {
        return {
            currentindex: 0,
            addisclick: false,
            tablist: [],
            grouplist: [],
            addgroupisclick: false,
        }
    },
    mounted() {
        var liffID = '1655851081-DP32Go8k';

        liff.init({
            liffId: liffID
        }).then(function () {
            console.log('LIFF init');
            console.log(liff.getLanguage());
            // 這邊開始寫使用其他功能

        }).catch(function (error) {
            console.log(error);
        });
        this.getuserprofile();
        axios.get('https://script.google.com/macros/s/AKfycbyhdWHsD54cmDEw-9pYlBcKeciZSH5xlcVDRnge5fJaoi7OOCyHQntMCtHjgs2uc8VptQ/exec')
            .then((res) => {
                this.tablist = res.data
                console.table(res.data)
            })
            .catch((error) => {
                console.error(error)
            })
        axios.get('https://script.google.com/macros/s/AKfycbwihpL99dszQMLvEHRfLUrnms_eLQsAlF_HOTmYXtzMXTv0putkRm0NSCP9_v1VQCc4bg/exec')
            .then((res) => {
                this.grouplist = res.data
                console.table(res.data)
            })
            .catch((error) => {
                console.error(error)
            })

    },
    methods: {
        getuserprofile() {
            var context = liff.getContext();
            console.log(context);

            // 取得使用者公開資料
            // 後台的「Scopes」要設定開啟 profile, openid
            liff.getProfile()
                .then(function (profile) {
                    console.log(profile);
                });

            // 取得使用者 email
            // 後台的 Email address permission 要是「Applied」
            // LIFF 的設定，Scopes 的「email*」要打勾
            // 使用者在登入時，「電子郵件帳號」也要是「許可」的
            var user = liff.getDecodedIDToken();
            var email = user.email;
            console.log(email);
        },
        tabclick(indx) {
            this.currentindex = indx;
        },
        addtab() {
            this.addisclick = true
            this.addgroupisclick = false;
        },
        closeaddtab() {
            this.addisclick = false

        },
        addgroup() {
            this.addgroupisclick = true
        },
        close() {
            this.addgroupisclick = false
            console.log('123')
        }
    },
    components: {
        TAB,
        Windowtab,
        addtab,
        addgroup
    }
}
</script>

