<template>
<div class="addtab">
    <div class="close" @click="closeaddtab">
        <i class="ri-close-line"></i>
    </div>

    <div class="name">
        <span class="title">名稱</span>
        <input type="text" v-model="tabname">
    </div>
    <div class="url">
        <span class="title">網址</span>
        <input type="text" v-model="url">
    </div>
    <div class="addtabbtn" @click="addtab(groupindex)">
        <span>新增頁籤</span>
    </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
    props:['groupindex'],
    data() {
        return {
            tabname:'',
            url:''
        }
    },
    methods: {
        closeaddtab() {
            this.$emit('closeaddtabhandler')
        },
        addtab(indx) {
            axios.get('https://script.google.com/macros/s/AKfycbweyQvztDp2zQjiSQ0oGaNMkxS7pHTUfcHcd8LDIKmPkjrsrHL9O1htkT1cpgQwrJCs8w/exec', {
                    params: {
                        tabname: this.tabname,
                        url: this.url,
                        group: indx
                    }

                })
                .then((res) => {
                    console.table(res.data)
                })
                .catch((error) => {
                    console.error(error)
                })
            this.$emit('closeaddtabhandler')
        }
    }
}
</script>

