<template>
<div class="addgroup">
    <div class="wrap">
        <span>群組名稱</span>
        <input type="text" v-model="groupname">
    </div>

    <div class="btnwrap">
        <div class="add" @click.stop="sendgroup">
            增加
        </div>
        <div class="cancel" @click.stop="emitclose">
            取消
        </div>
    </div>

</div>
</template>

<script>
import axios from 'axios'
export default {
    props: ['groupid'],
    data() {
        return {
            groupname: ''
        }
    },
    methods: {
        sendgroup() {
            axios.get('https://script.google.com/macros/s/AKfycbwmiW2VHubQJEn-Nbwkl5ECH08q3XjoIOozJXMeRd-8Uv4lkVROC-0kpkD0F3Lkbk-jng/exec', {
                    params: {
                        groupid: '1',
                        groupname: this.groupname,
                    }

                })
                .then((res) => {
                    console.table(res.data)
                })
                .catch((error) => {
                    console.error(error)
                })

            this.$emit('closehandler', this.groupname)

        },
        emitclose() {
            this.$emit('closehandler')
        }
    }
}
</script>

